import React from 'react';
import { Link } from 'react-router-dom'; // <-- Import Link  to iu hng
import { useAuth } from '../context/AuthContext';

// Danh sch cc vai tr admin
const ADMIN_ROLES = ['Ca Hng', 'Nh My Tn', 'ad mind'];

function WelcomePage() {
  // Ly thng tin user
  const { user } = useAuth();

  // Kim tra xem user c phi l admin khng
  const isAdmin = user && ADMIN_ROLES.includes(user.phan_loai);

  // function HomePage()  c i thnh function WelcomePage()
  // export default HomePage  c i thnh export default WelcomePage
  return (
    <div style={{ padding: '1rem' }}>
      <h1>Cho mng tr li, {user?.name}!</h1>
      
      <p>Bn  ng nhp vi vai tr l: <strong>{user?.phan_loai}</strong></p>

      <div style={{ marginTop: '2rem', display: 'flex', flexDirection: 'column', gap: '1rem', alignItems: 'flex-start' }}>
        <h3>Li tt nhanh:</h3>
        
        {/* Link n trang sn phm, ai ng nhp cng thy */}
        <Link to="/products" style={{ fontSize: '1.2rem' }}>
          Xem Bng Gi Sn Phm
        </Link>
        
        {/* Link n trang Dashboard, ch admin mi thy */}
        {isAdmin && (
          <Link to="/dashboard" style={{ fontSize: '1.2rem' }}>
            Truy cp Dashboard Qun tr
          </Link>
        )}
      </div>
    </div>
  );
}

export default WelcomePage;