import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useAuth } from '../../context/AuthContext';
import './Header.css';

function Header() {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const { user, logout } = useAuth();
  const navigate = useNavigate();

  const handleLogout = () => {
    logout();
    setIsMobileMenuOpen(false);
    navigate('/login');
  };

  return (
    <header className="header">
      <div className="header-logo">
        <Link to="/" onClick={() => setIsMobileMenuOpen(false)}>
          DURAflex  Ty Nguyn
        </Link>
      </div>

      {/* --- Menu cho my tnh --- */}
      <nav className="header-nav-desktop">
        {user ? (
          <>
            <span>Cho, {user.name}</span>
            <button className="logout-button" onClick={handleLogout}>ng xut</button>
          </>
        ) : (
          <Link to="/login">ng nhp</Link>
        )}
      </nav>

      {/* --- Nt Hamburger v Menu cho in thoi --- */}
      <button 
        className="hamburger-menu" 
        onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
      >
        {isMobileMenuOpen ? '' : ''}
      </button>

      {/* Thm class 'open' khi isMobileMenuOpen l true */}
      <nav className={`header-nav-mobile ${isMobileMenuOpen ? 'open' : ''}`}>
        {user ? (
          // KHI  NG NHP
          <>
            <span>Cho, {user.name} ({user.phan_loai})</span>
            <button className="logout-button" onClick={handleLogout}>ng xut</button>
          </>
        ) : (
          // KHI CHA NG NHP (PHN B THIU)
          <Link to="/login" onClick={() => setIsMobileMenuOpen(false)}>ng nhp</Link>
        )}
      </nav>
    </header>
  );
}

export default Header;